@use 'utility' as *;

.button {
	border: none;
	display: flex;
	height: 7.2rem;

	& span {
		height: 100%;
	}

	&-text {
		width: 20.4rem;
		padding: 2.1rem 0;
		font-family: var(--font-shoulder);
		text-transform: uppercase;
		letter-spacing: 3.64px;
		color: var(--white);
		background-color: var(--black);
	}

	&-img {
		width: 5.6rem;
		padding: 2.4rem;
		background-color: var(--gold);
	}
}
