*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
}

html,
body {
  height: 100%;
}

body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

input,
button,
textarea,
select {
  font: inherit;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

:root {
  --gold: hsl(24, 56%, 63%);
  --black: hsl(0, 0%, 8%);
  --gray: hsl(0, 0%, 27%);
  --white: hsl(0, 0%, 100%);
}

:root {
  --font-shoulder: "Big Shoulders Display", cursive;
  --font-outfit: "Outfit", sans-serif;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.8rem;
  font-family: var(--font-outfit);
}

.heading-primary {
  font-size: 6rem;
  font-family: var(--font-shoulder);
  text-transform: uppercase;
  line-height: 0.92;
}
@media (min-width: 36em) {
  .heading-primary {
    font-size: 7rem;
  }
}
@media (min-width: 64em) {
  .heading-primary {
    position: relative;
    font-size: 9rem;
  }
}
.heading-primary span {
  display: block;
}
@media (min-width: 64em) {
  .heading-primary .modern {
    position: relative;
    white-space: nowrap;
  }
  .heading-primary .modern::before {
    content: attr(data-split);
    position: absolute;
    top: 0;
    left: 0;
    color: white;
    width: 49%;
    overflow: hidden;
  }
}
@media (min-width: 64em) {
  .heading-primary .art-gallery {
    position: relative;
    white-space: nowrap;
  }
  .heading-primary .art-gallery::before {
    content: attr(data-split);
    position: absolute;
    top: 0;
    left: 0;
    color: white;
    width: 49%;
    overflow: hidden;
  }
}

.button {
  border: none;
  display: flex;
  height: 7.2rem;
}
.button span {
  height: 100%;
}
.button-text {
  width: 20.4rem;
  padding: 2.1rem 0;
  font-family: var(--font-shoulder);
  text-transform: uppercase;
  letter-spacing: 3.64px;
  color: var(--white);
  background-color: var(--black);
}
.button-img {
  width: 5.6rem;
  padding: 2.4rem;
  background-color: var(--gold);
}

.hero {
  position: relative;
  margin-bottom: 12rem;
}
.hero__img {
  background-color: var(--black);
  width: 100%;
}
@media (min-width: 48em) {
  .hero__img {
    width: 43.7rem;
  }
}
@media (min-width: 64em) {
  .hero__img {
    width: 90rem;
  }
}
@media (min-width: 75em) {
  .hero__img {
    width: 99rem;
  }
}
.hero__img-inner {
  width: 100%;
}
@media (min-width: 48em) {
  .hero__img-inner {
    width: auto;
  }
}
@media (min-width: 64em) {
  .hero__img-inner {
    margin-left: auto;
  }
}
.hero__content {
  margin: 3.2rem 1.6rem 0;
}
@media (min-width: 36em) {
  .hero__content {
    margin-left: 6rem;
    margin-right: 6rem;
  }
}
@media (min-width: 48em) {
  .hero__content {
    width: 34rem;
    position: absolute;
    margin: 0 4rem;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
  }
}
@media (min-width: 60em) {
  .hero__content {
    margin: 0 9rem;
  }
}
@media (min-width: 64em) {
  .hero__content {
    display: flex;
    width: auto;
    margin: 0;
    top: 30%;
    right: 50%;
    transform: translateX(50%);
  }
}
@media (min-width: 75em) {
  .hero__content {
    column-gap: 18rem;
  }
}
.hero__title {
  margin-bottom: 3.2rem;
}
@media (min-width: 48em) {
  .hero__title {
    margin-bottom: 4.8rem;
  }
}
@media (min-width: 64em) {
  .hero__title {
    width: 51.4rem;
  }
}
@media (min-width: 64em) {
  .hero__inner {
    width: 35rem;
  }
}
@media (min-width: 75em) {
  .hero__inner {
    padding-left: 6rem;
  }
}
.hero__description {
  margin-bottom: 3.2rem;
}
@media (min-width: 48em) {
  .hero__description {
    margin-bottom: 4.8rem;
  }
}